import CommonFunctions from '../../utils/commonFunctions';
const Helpers = () => {
    const _createNewGroup = (label, id = CommonFunctions.generateId()) => ({
        id,
        label,
    });
    const addGroup = (groups, label, id = CommonFunctions.generateId()) => CommonFunctions.isDuplicate(groups, label)
        ? groups
        : [...groups, _createNewGroup(label, id)];
    const deleteGroup = (groups, id) => groups.filter(group => group.id !== id);
    return { _createNewGroup, addGroup, deleteGroup };
};
export default Helpers();
