import { writable, derived } from 'svelte/store';
import Helpers from './itemMap.helpers';
import { itemMap } from '../../data';
const { subscribe, update, set } = writable(itemMap);
// const { subscribe, update, set } = writable({} as ItemMapModel);
// DERIVED
const RelationsDerived = derived({ subscribe }, (itemMap) => Helpers.getRelationsMap(itemMap));
const SelectedItemIdDerived = derived({ subscribe }, (itemMap) => Helpers.getSelectedItemId(itemMap));
// UPDATE
const toggleItem = (id) => update(itemMap => Helpers.toggleItem(itemMap, id));
const addItem = (groupId, label) => update(itemMap => Helpers.addItem(itemMap, groupId, label));
const deleteItemFromGroup = (id) => update(itemMap => Helpers.deleteItem(itemMap, id));
const removeGrouplessItems = (groupIds) => update(itemMap => Helpers.removeGrouplessItems(itemMap, groupIds));
const toggleChild = (parentId, childId) => update(itemMap => Helpers.toggleChild(itemMap, parentId, childId));
// TODO: consider placing updates in actions for readability
// const actions = {
//   toggleItem: toggleItem,
//   deleteItemFromGroup: deleteItemFromGroup,
//   removeGrouplessItems: removeGrouplessItems,
// };
// EXPORTS
const ItemsMapStore = {
    subscribe,
    toggleItem,
    addItem,
    deleteItemFromGroup,
    removeGrouplessItems,
    toggleChild,
    set,
};
export { ItemsMapStore, RelationsDerived, SelectedItemIdDerived };
