<script lang="ts">
import { createEventDispatcher } from 'svelte';

export let placeholder = '';

const dispatch = createEventDispatcher();
let input = '';

const onClick = (): void => {
  if (input === '' || input === ' ') {
    return;
  }

  dispatch('add', input);
  input = '';
};
</script>

<div class="Input">
  <input class="field" type="text" bind:value={input} {placeholder} />
  <button class="button" on:click={onClick}>Add</button>
</div>

<style lang="scss">
.Input {
  display: flex;
  height: 3rem;
}

.field {
  margin-right: 1rem;
}

.button {
  cursor: pointer;
  padding: 0.4rem;
}
</style>
